.type {
	margin: 0 auto;
	text-align: center;
	&[data-columns="3"] {
		max-width: 720px; }
	&[data-columns="4"] {
		max-width: 880px; }
	&__row {
		margin: -20px;
		font-size: 0; }
	&__col {
		display: inline-block;
		vertical-align: top;
		width: 200px;
		padding: 20px;
		&_md {
			width: 240px; } }
	&__btn {
		display: block;
		padding: 20px 5px 10px;
		border: 2px solid $text;
		border-radius: 2px;
		width: 100%;
		color: $text;
		background: $white;
		user-select: none;
		transform: translate3d(0, 0, 0);
		&:disabled,
		&.is-disabled {
			opacity: 0.5;
			pointer-events: none; }
		&-icon {
			display: block;
			font-size: 0;
			.icon {
				fill: $orange;
				color: $darkGray;
				font-size: 86px; }
			&_fill {
				.icon {
					color: $white;
					fill: $blue;
					stroke: none; } }
			img {
				display: inline-block;
				vertical-align: top;
				height: 86px; } }
		&-title {
			display: block;
			padding-top: 10px;
			height: 48px;
			@include vertical;
			span {
				display: inline-block;
				vertical-align: middle;
				font-size: 16px;
				line-height: 1; } } } }
