* {
  font-family: 'Roboto', Arial, sans-serif;
}

html,
body,
.main {
  position: relative;
}

body {
  min-width: 600px;
  color: #6e6e6e;
  background: #fff;
  font-size: 14px;
  line-height: 1.4;
}

.main {
  margin: 0 auto;
  max-width: 1200px;
}

.row {
  margin: -40px -20px;
}

.row:after {
  content: " ";
  display: table;
  clear: both;
}

.col {
  position: relative;
  float: left;
  padding: 40px 20px;
  width: 50%;
}

@media only screen and (max-width: 979px) {
  .col {
    width: 100%;
  }
}

.col:nth-child(1):before {
  right: 0;
}

@media only screen and (max-width: 979px) {
  .col:nth-child(2) {
    border-top: 1px solid #eee;
  }
}

.col:nth-child(2):before {
  left: -1px;
}

.col:before {
  content: '';
  position: absolute;
  top: -20px;
  bottom: -20px;
  border-left: 1px solid #eee;
}

@media only screen and (max-width: 979px) {
  .col:before {
    display: none;
  }
}

.sm-container {
  margin: -20px;
}

.sm-section {
  padding: 40px 40px;
  margin: 20px auto;
  border: 1px solid #eee;
  max-width: 880px;
}

@media only screen and (max-width: 979px) {
  .sm-section:first-child {
    border-top: none;
  }
}

@media only screen and (max-width: 979px) {
  .sm-section {
    margin: 0;
    border-left: none;
    border-right: none;
    border-bottom: none;
    max-width: unset;
  }
}

.text-uppercase {
  text-transform: uppercase;
}

.color-red {
  color: #ec4a41;
}

.color-green {
  color: #22B745;
}

.error-message {
  padding: 5px 0 4px;
  opacity: 0;
  visibility: hidden;
  color: #ec4a41;
  font-size: 14px;
  line-height: 1.2;
  transition: opacity 0.17s ease-in-out, visibility 0.17s ease-in-out;
}

.is-error .error-message {
  opacity: 1;
  visibility: visible;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 10px;
  margin: 0;
  border: none;
  min-width: 40px;
  height: 40px;
  color: #2e2e2e;
  background: #eee;
  font-size: 16px;
  font-weight: 300;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  transition: background 0.175s ease, box-shadow 0.175s ease, opacity 0.175s ease;
  cursor: pointer;
  user-select: none;
}

.btn:active {
  color: #2e2e2e;
  background: #dadada;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_block {
  width: 100%;
}

.btn_lg {
  padding: 0 15px;
  min-width: 60px;
  height: 60px;
  font-size: 19px;
  line-height: 60px;
}

.btn_green {
  color: #fff;
  background: #22B745;
}

.btn_green:active {
  color: #fff;
  background: #22B745;
}

.btn_red {
  color: #fff;
  background: #ec4a41;
}

.btn_red:active {
  color: #fff;
  background: #ec4a41;
}

.btn_blue {
  color: #fff;
  background: #2e99db;
}

.btn_blue:active {
  color: #fff;
  background: #2e99db;
}

.btn_darkgray {
  color: #fff;
  background: #2e2e2e;
}

.btn_darkgray:active {
  color: #fff;
  background: #2e2e2e;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.btn-delete {
  display: inline-block;
  vertical-align: top;
  width: 60px;
  height: 60px;
  background: none;
  font-size: 0;
  line-height: 60px;
}

.btn-delete:active svg {
  fill: #cc1e14;
}

.btn-delete svg {
  display: inline-block;
  vertical-align: middle;
  fill: #ec4a41;
  font-size: 18px;
}

.simple-input {
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  border: 1px solid #eee;
  width: 100%;
  height: 60px;
  color: #2e2e2e;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  transition: border-color 0.17s ease-in-out;
}

.simple-input:focus {
  border-color: #2e99db;
}

.input-box {
  position: relative;
}

.input-box.is-error input {
  border-color: #ec4a41;
}

.input-box:hover .input-box__clear {
  opacity: 1;
}

.input-box input:focus + .input-box__clear {
  opacity: 1;
}

.input-box__clear {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 60px;
  height: 100%;
  opacity: 0;
  color: #2e2e2e;
  background: none;
  font-size: 16px;
  transition: opacity 0.2s ease-in-out, color 0.175s ease-in-out;
}

.input-box__clear:active {
  color: #2e2e2e;
  background: none;
}

.input-box__clear .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.input-box__input {
  padding-right: 60px;
}

.input-box__error-message {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.simple-select {
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  border: 1px solid #eee;
  border-radius: 0;
  width: 100%;
  height: 60px;
  color: #2e2e2e;
  background: none;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  transition: border-color 0.17s ease-in-out;
  appearance: none;
}

.simple-select:focus {
  border-color: #2e99db;
}

.simple-select::-ms-expand {
  display: none;
}

.select-box {
  position: relative;
}

.select-box:before {
  content: '';
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -4px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 8px 6px 0 6px;
  border-color: #2e2e2e transparent transparent transparent;
  pointer-events: none;
}

.select-box__select {
  padding-right: 30px;
}

.form {
  position: relative;
  margin: 0 auto;
  max-width: 460px;
  color: #6e6e6e;
}

.form_md {
  max-width: 800px;
}

@media only screen and (max-width: 767px) {
  .form_md {
    max-width: 460px;
  }
}

.form_type[data-type-buttons="1"] {
  padding-right: 200px;
  max-width: 660px;
}

.form_type[data-type-buttons="2"] {
  padding-right: 400px;
  max-width: 860px;
}

@media only screen and (max-width: 979px) {
  .form_type[data-type-buttons="2"] {
    padding-right: 0;
    max-width: 460px;
  }
}

@media only screen and (max-width: 979px) {
  .form_type[data-type-buttons="2"] .form__type {
    position: relative;
    top: auto;
    left: auto;
  }
}

.form_type .form__type {
  position: absolute;
  top: -4px;
  right: 0;
}

.form__label {
  display: block;
  padding-bottom: 8px;
  font-size: 16px;
  font-weight: 300;
  line-height: 18px;
}

.form__text {
  position: relative;
  padding: 8px 0 8px 20px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.form__text .icon {
  position: absolute;
  top: 12px;
  left: 0;
  font-size: 14px;
}

.form__field {
  max-width: 460px;
  margin: 0 auto;
}

.form__field:after {
  content: " ";
  display: table;
  clear: both;
}

.form__field:not(:last-child) {
  padding-bottom: 30px;
}

.form__field_line {
  margin: 0 -5px;
  font-size: 0;
}

.form__field_line .form__label, .form__field_line .form__text {
  display: inline-block;
  vertical-align: middle;
  padding: 0 5px;
}

.form__field_line .form__hidden {
  padding: 8px 5px 0;
}

.form__field_wide {
  max-width: unset;
}

.form a {
  color: #2e99db;
  font-weight: 300;
  transition: color 0.25s ease;
}

.form a:hover {
  color: #2183bf;
}

.form__hidden {
  padding-top: 8px;
}

.form__remove {
  float: right;
  min-width: 140px;
}

.form__title {
  padding-bottom: 20px;
  color: #2e2e2e;
  font-size: 22px;
  font-weight: 500;
  line-height: 24px;
}

.form__box {
  padding: 30px;
  border: 1px solid #eee;
}

.form__box + .form__box {
  margin-top: 20px;
}

.form__row {
  margin: 0 -20px;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .form__row {
    margin: 0;
  }
}

.form__row:not(:last-child) {
  padding-bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .form__row:not(:last-child) {
    padding-bottom: 0;
  }
}

.form__col, .form__divider {
  display: inline-block;
  vertical-align: bottom;
}

.form__col {
  padding: 0 20px;
  width: 50%;
}

@media only screen and (max-width: 767px) {
  .form__col {
    display: block;
    padding: 0 0 30px;
    width: 100%;
  }
}

.form__col .form__field_line {
  padding: 18px 0;
}

@media only screen and (max-width: 767px) {
  .form__col .form__field_line {
    padding: 0;
  }
}

.form__divider {
  padding-bottom: 30px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__col + .form__divider {
  padding: 21px 0;
  margin: 0 -20px;
  width: 40px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .form__col + .form__divider {
    display: block;
    padding: 0 0 30px;
    margin: 0;
    width: auto;
    text-align: left;
  }
}

.form__line {
  position: relative;
  padding-right: 54px;
}

.form__line:not(:last-child) {
  margin-bottom: 20px;
}

@media only screen and (max-width: 767px) {
  .form__line:not(:last-child) {
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid #eee;
  }
}

@media only screen and (max-width: 767px) {
  .form__line {
    padding-right: 0;
  }
}

.form__delete {
  position: absolute;
  right: -14px;
  bottom: 0;
}

@media only screen and (max-width: 767px) {
  .form__delete {
    position: relative;
    right: auto;
    bottom: auto;
  }
}

.form__type {
  padding-top: 30px;
}

.field {
  position: relative;
  padding-right: 60px;
}

.field + .field {
  margin-top: 15px;
}

.field__label {
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 18px;
  transform: translate3d(0, -50%, 0);
}

.field__delete {
  position: absolute;
  top: 0;
  right: 0;
}

.steps {
  position: relative;
  padding: 20px;
  color: #c1c1c1;
  font-size: 0;
}

.steps__header {
  position: relative;
}

.steps__header:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 13px;
  margin-bottom: -2px;
  border-bottom: 4px solid #c1c1c1;
  width: 100%;
}

.steps__list {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.steps__item {
  position: relative;
  z-index: 1;
  display: table-cell;
  vertical-align: bottom;
  table-layout: fixed;
  overflow: hidden;
  font-size: 14px;
  text-align: center;
}

.steps__item:last-child .steps__icon-active:after {
  width: 50%;
}

.steps__in {
  display: block;
  color: #c1c1c1;
}

.steps__in:hover .steps__title,
.steps__in:hover .steps__description {
  color: #FF6431;
}

.steps__in[data-step-state*="active"] .steps__title,
.steps__in[data-step-state*="active"] .steps__description {
  color: #FF6431;
}

.steps__in[data-step-state*="active"] .steps__icon-in,
.steps__in[data-step-state*="active"] .steps__icon-active {
  transition-delay: 0.25s;
}

.steps__in[data-step-state*="active"] .steps__icon-in {
  transform: translate3d(calc(-50% + 13px), 0, 0);
}

.steps__in[data-step-state*="active"] .steps__icon-active {
  transform: translate3d(calc(50% - 13px), 0, 0);
}

.steps__in[data-step-state*="done"] .steps__icon-in,
.steps__in[data-step-state*="done"] .steps__icon-active {
  transform: translate3d(0, 0, 0);
}

.steps__title, .steps__description {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: color 0.225s ease-in-out;
}

.steps__title {
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

@media only screen and (max-width: 979px) {
  .steps__title {
    font-size: 16px;
  }
}

@media only screen and (max-width: 767px) {
  .steps__title {
    font-size: 14px;
  }
}

.steps__description {
  height: 18px;
  font-size: 16px;
  font-weight: 300;
  line-height: 18px;
}

@media only screen and (max-width: 979px) {
  .steps__description {
    font-size: 14px;
  }
}

@media only screen and (max-width: 767px) {
  .steps__description {
    font-size: 12px;
  }
}

.steps__item-circle:before, .steps__progress-circle:before {
  content: '';
  display: block;
  margin: 0 auto;
  border-radius: 50%;
  width: 26px;
  height: 26px;
}

.steps__item-circle:before {
  background: #c1c1c1;
}

.steps__progress-circle:before {
  background: #FF6431;
}

.steps__item-circle {
  margin-top: 8px;
}

.steps__progress {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px;
  color: #FF6431;
  pointer-events: none;
}

.steps__progress-line, .steps__progress-in {
  transition: transform 0.3s ease-in-out;
}

.steps__progress-line {
  overflow: hidden;
  transform: translateX(-100%);
}

.steps__progress-in {
  position: relative;
  transform: translateX(100%);
}

.steps__progress-in:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 13px;
  margin-bottom: -2px;
  border-bottom: 4px solid #FF6431;
  width: 100%;
}

.steps__progress-row {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.steps__progress-circle {
  display: table-cell;
  vertical-align: top;
}

.footer {
  padding: 40px 20px 20px;
  font-size: 0;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__left {
  float: left;
}

.footer__right {
  float: right;
}

.footer .btn + .btn {
  margin-left: 20px;
}

.footer .btn {
  min-width: 120px;
}

.box {
  position: relative;
  padding: 49px 0 0;
  margin: 0 20px;
  border: 1px solid #eee;
  height: 100%;
}

.box__heading {
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  padding: 0 20px;
  height: 50px;
  color: #fff;
  background: #2e2e2e;
  text-align: center;
  font-size: 0;
  text-align: left;
}

.box__heading:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.box__heading span {
  display: inline-block;
  vertical-align: middle;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.3;
}

@media only screen and (max-width: 979px) {
  .box__heading span {
    font-size: 18px;
  }
}

@media only screen and (max-width: 767px) {
  .box__heading span {
    font-size: 16px;
  }
}

.box__inner {
  overflow: auto;
  padding: 40px 20px;
  max-height: calc(100vh - 295px);
}

.type {
  margin: 0 auto;
  text-align: center;
}

.type[data-columns="3"] {
  max-width: 720px;
}

.type[data-columns="4"] {
  max-width: 880px;
}

.type__row {
  margin: -20px;
  font-size: 0;
}

.type__col {
  display: inline-block;
  vertical-align: top;
  width: 200px;
  padding: 20px;
}

.type__col_md {
  width: 240px;
}

.type__btn {
  display: block;
  padding: 20px 5px 10px;
  border: 2px solid #6e6e6e;
  border-radius: 2px;
  width: 100%;
  color: #6e6e6e;
  background: #fff;
  user-select: none;
  transform: translate3d(0, 0, 0);
}

.type__btn:disabled, .type__btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.type__btn-icon {
  display: block;
  font-size: 0;
}

.type__btn-icon .icon {
  fill: #FF6431;
  color: #2e2e2e;
  font-size: 86px;
}

.type__btn-icon_fill .icon {
  color: #fff;
  fill: #2e99db;
  stroke: none;
}

.type__btn-icon img {
  display: inline-block;
  vertical-align: top;
  height: 86px;
}

.type__btn-title {
  display: block;
  padding-top: 10px;
  height: 48px;
  text-align: center;
  font-size: 0;
}

.type__btn-title:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.type__btn-title span {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  line-height: 1;
}

.field-box {
  padding: 20px 15px;
  border: 1px solid #eee;
  color: #2e2e2e;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

[data-scrollbar="default"]::-webkit-scrollbar {
  display: initial !important;
}

[data-scrollbar="default"]::-webkit-scrollbar:vertical {
  width: 7px;
  background: linear-gradient(to right, transparent 3px, #ff8a64 3px, #ff8a64 4px, transparent 4px);
}

[data-scrollbar="default"]::-webkit-scrollbar:horizontal {
  height: 7px;
  background: linear-gradient(to bottom, transparent 3px, #ff8a64 3px, #ff8a64 4px, transparent 4px);
}

[data-scrollbar="default"]::-webkit-scrollbar-thumb {
  background: #FF6431;
}

.no-touch [data-scrollbar="default"]::-webkit-scrollbar-thumb:hover {
  background: #fd3f00;
}

.modals-list {
  padding: 20px;
}

.modals-list a {
  color: #2e99db;
  font-size: 18px;
  line-height: 24px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  font-size: 0;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal_sm .modal__container {
  max-width: 480px;
}

.modal_md .modal__container {
  max-width: 680px;
}

.modal__container {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 1200px;
  white-space: normal;
  text-align: left;
}

.modal-content {
  border-radius: 5px;
  background: #fff;
  box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.2);
}

.modal-content__heading {
  padding: 13px 20px;
  border-radius: 5px 5px 0 0;
  color: #fff;
  background: #FF6431;
  font-size: 24px;
  font-weight: 500;
  line-height: 1;
}

.modal-content__body {
  padding: 20px;
  color: #6e6e6e;
}

.modal-content__body p {
  font-size: 16px;
  line-height: 24px;
}

.modal-content__body p:not(:last-child) {
  margin-bottom: 20px;
}

.modal-content__footer {
  padding: 20px;
  border-top: 1px solid #bdc3c7;
}

.modal-content__footer:after {
  content: " ";
  display: table;
  clear: both;
}

.modal-content__submit {
  float: right;
  min-width: 125px;
}

.modal-content__submit:not(:first-child) {
  margin-right: 20px;
}

.modal-item {
  position: relative;
}

.modal-item + .modal-item {
  margin-top: 20px;
}

.modal-item__label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  line-height: 60px;
}

.modal-item__label span {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  line-height: 1.2;
}

.modal-item__label + .modal-item__row {
  padding-left: 100px;
}

.modal-item__row {
  margin: 0 -10px;
}

.modal-item__row:after {
  content: " ";
  display: table;
  clear: both;
}

.modal-item__col {
  float: left;
  padding: 0 10px;
  width: 50%;
}
